

const HOST = "https://quranapi-1-e0491334.deta.app"
//const HOST = "http://127.0.0.1:8000" // for dev

export const QURANAPI_URL = HOST + "/"
export const REPO_URL = "http://github.com/xuraan/"

export const VERSION_URL =  (local: string) => HOST + "/quran_app_api/versions/?local=" + local
export const FEATURES_URL = (local: string) => HOST + "/quran_app_api/features/?local=" + local
export const QUESTIONS_URL = (local: string) => HOST + "/quran_app_api/questions/?local=" + local
export const ANSWER_URL = (key: string, local: string) => HOST + "/quran_app_api/answers/"+ key +"?local=" + local

export const LOCALS_URL = HOST + "/quran_app_api/locals/"
export const LOCALIZED_STRINGS_DICT_URL = (localCode: string) => HOST + "/quran_app_api/locals/" + localCode + "/localized_strings_dict";




export const fetchLocals = async () => {
    const response = await fetch(LOCALS_URL);
    return await response.json();
}