import './App.css';
import './styles/app.css';
import AboutSection from './components/Sections/AboutSection';
import Topbar from './components/Topbar';
import { useAppSelector } from './app/hooks';
import { selectedLocal } from './features/local/localSlice';
import VersionSection from './components/Sections/VersionsSection';
import SupportSection from './components/Sections/SupportSection';


/**
 * The main App component.
 * @function
 * @returns {JSX.Element} The JSX element to be rendered.
 */
function App(): JSX.Element {
  const local = useAppSelector(selectedLocal);

  return (
    <div dir={ local.key === 'ar' ? "rtl" : 'ltr'} className=" container-fluid col-lg-8 col-12">
      <Topbar />
      <div className='d-flex  justify-content-center'>
        <main data-bs-spy="scroll" data-bs-target="#navbar-example2" data-bs-root-margin="0px 0px -40%" data-bs-smooth-scroll="true" className="" tabIndex={0}>
          <AboutSection />
          <VersionSection />
          <SupportSection />
        </main>
      </div>
    </div>
  );
}

export default App;
